import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { createPage, base } from 'nc-lightapp-front';
import * as actions from '../action/action';
import * as events from '../event/event';
//新的筛选
//import Org from '../../../../uapbd/refer/org/FinanceOrgTreeRef/index.js';
//下载
import EntryConfigRef from '../../../refer/hpfrefs/HpfEntryConfigGridRef/index';
//导入导出
import excelImportconfig from '../../../../uap/common/components/excelImportconfig';
import ExcelOutput from '../../../../uap/common/components/ExcelOutput';
const searchid = 'search';
const { NCDiv } = base;

var excelAppCode;
var excelPageCode;
var excelPkOrg;
var treeId = 'chargetypeTree';//树组件Id
var formId = 'chargetype';//卡片组件Id
var pageCode = "M0Z10207_chargetype"; // 小应用页面编码
var multiInit = 'M0Z10207'; //多语资源编码
var domainName = 'hpf';  // 模块编码

/**
* 职务类别-全局
*/
class comp extends Component {
    constructor(props) {
        props.use.form(formId)
        super(props)
        this.state = {
            disabledSearch: false, //新的筛选
            //******************** */
            // org: {
            //     value: undefined,
            //     queryCondition: () => {//组织权限
            //         return {
            //             TreeRefActionExt: 'nccloud.web.refer.sqlbuilder.PrimaryOrgSQLBuilder'
            //         };
            //     },
            //     onChange: (val) => {
            //         this.state.org.value = val;
            //         this.setState(this.state, () => {
            //             this.props.search.setSearchValByField(searchid, 'pk_org', { display: this.state.org.value.refname, value: this.state.org.value.refpk });
            //             actions['loadTreeDataByOrg'].bind(this)();
            //         });

            //     }
            // },
            //*********************/
            onChange: (val) => {
                this.state.org.value = val;
                this.setState(this.state, () => {
                    this.props.search.setSearchValByField(searchid, 'pk_org', { display: this.state.org.value.refname, value: this.state.org.value.refpk });
                    actions['loadTreeDataByOrg'].bind(this)();
                });

            },
            json: {},
            pk_org: ''
        }
        //自定义根节点
        this.root = {
            "isleaf": false,
            "key": "root",
            "title": this.state.json['M0Z10207-000000'],/* 国际化处理： 职务类别*/
            "id": "root",
            "innercode": "root",
            "pid": "",
            "refname": this.state.json['M0Z10207-000000'],/* 国际化处理： 职务类别*/
            "refpk": "~"
        };
        actions['afterInit'](this);
    }

    /**
    * react 生命周期函数 组件渲染完后触发事件
    */
    componentDidMount() {
        // 保存json和inlt到页面state中并刷新页面
        let callback = (json, status, inlt) => {
            if (status) {
                this.setState({ json, inlt }, () => {
                    this.initTemplate(this.props, () => {
                        actions['loadTreeData'].bind(this)()
                    })
                })
            }
        }
        this.props.MultiInit.getMultiLang({ moduleId: multiInit, domainName, callback })
    }

    /**
        * react 生命周期函数 组件渲染完后触发事件
        */
    componentWillMount() {
        // 保存json和inlt到页面state中并刷新页面
        let callback = (json, status, inlt) => {
            this.setState({ json: json, inlt: inlt }, () => {
                this.root.refname = this.root.title = this.state.json['M0Z10207-000000'];
                this.initTemplate(this.props, () => {
                    actions['loadTreeData'].bind(this)()
                })
            })
        }
        this.props.MultiInit.getMultiLang({ moduleId: multiInit, domainName, callback })
    }

    /**
    * 初始化：模板和按钮
    */
    initTemplate = (props, callback) => {
        props.createUIDom(
            {
                pagecode: pageCode//页面id
            },
            data => {
                if (data) {
                    if (data.template) {
                        if (data.context.pk_org != 'GLOBLE00000000000000') {
                            this.setState({ pk_org: data.context.pk_org });
                            this.setState({ org_Name: data.context.org_Name });
                        } else {
                            this.setState({ pk_org: '' });
                            this.setState({ org_Name: '' });
                        }
                        let meta = data.template;
                        this.modifierMeta(props, meta)
                        props.meta.setMeta(meta);

                        //新的筛选
                        // var context = data.context || {};
                        // if (context.pk_org != 'GLOBLE00000000000000') {
                        //     var value = {
                        //         refpk2: 'finance' + context.pk_org,
                        //         refpk: context.pk_org,
                        //         refname: context.org_Name
                        //     };
                        //     this.state.org.onChange(value);
                        // } else {
                        //     var value = {
                        //         refpk2: '',
                        //         refpk: '',
                        //         refname: ''
                        //     };
                        //     this.state.org.onChange(value);
                        // }
                        //根据个性化设置设置默认的业务单元
                        this.props.search.setSearchValByField(searchid, 'pk_org', { display: this.state.org_Name, value: this.state.pk_org });
                        //系统编码参照过滤
                        let metas = this.props.meta.getMeta();
                        for (var item of metas['chargetype'].items) {
                            if (item.refcode) {
                                if (item.attrcode === 'syscode') { //来源系统过滤
                                    item.queryCondition = () => {
                                        return {
                                            pk_org: this.props.search.getSearchValByField("search", "pk_org").value.firstvalue
                                        };
                                    };
                                }

                                // if (item.attrcode === 'pk_sssrxm') { //收入项目
                                //     item.queryCondition = () => {
                                //         return {
                                //             pk_org: this.state.pk_org
                                //         };
                                //     };
                                // }

                            }
                        }
                        this.props.meta.setMeta(metas);
                    }
                    if (data.button) {
                        props.button.setButtons(data.button, () => {
                            this.updateButtonStatus('browse');
                        });
                    }
                    //新的筛选
                    //  this.props.search.changeItemVisibleByField(searchid, 'pk_org', false);
                    //导入按钮 单据类型命名自定义，但要与xml配置文件名一致
                    excelAppCode = this.props.getAppCode();
                    //  excelPkOrg  = this.state.org.value.refpk;
                    excelPkOrg = this.props.search.getSearchValByField("search", "pk_org").value.firstvalue;
                    excelPageCode = this.props.getPageCode();
                    let excelimportconfig = excelImportconfig(props, 'hpf', 'chargetype', true, '', { appcode: excelAppCode, pk_org: excelPkOrg, pagecode: excelPageCode }, () => {
                        actions['onRefreshImport'].call(this) //导入完成后 执行刷新方法
                    });
                    props.button.setUploadConfig("import_excel", excelimportconfig);

                    callback && callback();
                }
            }
        )
    }


    updateButtonStatus = (status) => {
        this.props.button.setButtonsVisible({
            Save: status != 'browse',
            SaveAdd: status == 'add',
            Cancel: status != 'browse',
            Print: false,//根据业务需要放出
            Output: false,
            Refresh: status == 'browse'
        })
    }
    /**
    * 调整模板属性
    */
    modifierMeta(props, meta) {
        //查询区参照过滤
        meta[searchid].items.map((item) => {
            if (item.attrcode === 'pk_org') { //财务组织过滤
                //item.isMultiSelectedEnabled = true; //财务组织多选
                item.queryCondition = () => {
                    return {
                        funcode: props.getSearchParam('c'),//appcode获取
                        //用户权限过滤
                        TreeRefActionExt: 'nccloud.web.refer.sqlbuilder.PrimaryOrgSQLBuilder'
                    };
                };
            }

            if (item.attrcode === 'syscode') { //来源系统过滤
                item.queryCondition = () => {
                    return {
                        pk_org: this.props.search.getSearchValByField("search", "pk_org").value.firstvalue
                    };
                };
            }

        });

        return meta;
    }

    buttonClick(props, id) {
        switch (id) {
            case 'Save':
                actions['onSaveButtonClick'].call(this)
                break
            case 'SaveAdd':
                actions['onSaveAddButtonClick'].call(this)
                break
            case 'Cancel':
                actions['onCancelButtonClick'].call(this)
                break
            case 'Refresh':
                actions['onRefreshButtonClick'].call(this)
                break
            case 'Down':
                props.modal.show('selds');
                break;
            case 'export_excel':
                actions['onExportTempButtonClick'].call(this)
                break
        }
    }

    /**
    * 渲染
    * @returns {*}
    */
    render() {
        const { syncTree, form, button, DragWidthCom, BillHeadInfo, search } = this.props;
        const { createBillHeadInfo } = BillHeadInfo; //新加 返回图标和按钮
        //DragWidthCom 平台出的左右布局的组件  专用于树卡和树表
        const { createSyncTree } = syncTree;//创建同步树 需要引入这个
        const { createForm } = form;//创建表单，需要引入这个
        const { createButtonApp } = button;

        //下载
        const { NCCreateSearch } = search;
        const { modal } = this.props;
        let { createModal } = modal;


        return (
            <div className="nc-bill-tree-card">
                {/* 头部 header*/}
                <NCDiv areaCode={NCDiv.config.HEADER} className="header">
                    {/* 标题 title*/}
                    <div className="title" >
                        {createBillHeadInfo({
                            title: this.state.json['M0Z10207-000023']/* 国际化处理： 职务类别-全局*/,
                            showBackBtn: false,
                            initShowBackBtn: false
                        }
                        )}
                    </div>

                    {/* 新的筛选*/}
                    {/* <div className="search-box">
                        {Org({
                            ...this.state.org,
                            disabled: this.state.editMode,
                            fieldid: 'org_fieldid'
                        })}
                    </div> */}
                    <div className="search-area-selfdef" style={{width:'570px'}}>
                        {NCCreateSearch(searchid, {//查询区 下载
                            //clickSearchBtn: actions['onClickSearchBtn'].bind(this),
                            showSearchBtn: false,
                            showClearBtn: false,
                            showAdvBtn: false,
                            showAdvSearchPlanBtn: false,
                            showAdvSearchPlanBtn: false,
                            onAfterEvent: actions['loadTreeDataByOrg'].bind(this, this.props),
                        })}
                    </div>

                    {/* 按钮组 btn-group*/}
                    <div className=" btn-group">
                        {createButtonApp({
                            area: 'header-action',
                            buttonLimit: 3,
                            onButtonClick: this.buttonClick.bind(this),
                        })}

                    </div>
                </NCDiv>

                {/* 树卡区域 */}
                <div className="tree-card">
                    <DragWidthCom
                        // 左树区域
                        leftDom={
                            <div className="tree-area">
                                {createSyncTree({
                                    treeId: treeId,
                                    needEdit: true, //不启用编辑
                                    showLine: false, //显示连线
                                    needSearch: false, //是否需要搜索框
                                    //onSelectedChange: this.onSelectTreeNodeChange.bind(this),//选择改变
                                    onSelectEve: events['onSelectTree'].bind(this),//选择
                                    onMouseEnterEve: events['onMouseEnterEve'].bind(this),
                                    clickEditIconEve: actions['onEditTreeNode'].bind(this), //编辑点击 回调
                                    clickAddIconEve: actions['onAddTreeNode'].bind(this), //新增点击 回调
                                    clickDelIconEve: actions['onDeleteTreeNode'].bind(this), // 删除点击 回调
                                    showModal: false,
                                    disabledSearch: this.state.disabledSearch || false
                                })}
                            </div>}     //左侧区域dom
                        // 右卡片区域
                        rightDom={
                            <div className="card-area">
                                {createForm(formId, {
                                    cancelPSwitch: true,
                                    onAfterEvent: events['onAfterFormEvent'].bind(this),
                                    onBeforeEvent: events['onBeforeFormEvent'].bind(this)
                                })
                                }
                            </div>}     //右侧区域dom
                        defLeftWid='280px'      // 默认左侧区域宽度，px/百分百
                    />
                </div>

                <ExcelOutput //导出组件
                    {...this.props}
                    moduleName='hpf'//模块名
                    billType='chargetype'//单据类型  命名自定义，但要与xml配置文件名一致  
                    selectedPKS={this.state.pks}
                    appcode={this.props.getAppCode()}
                    pagecode={this.props.getPageCode()}
                />

                {
                    createModal('selds', {
                        title: '选择来源系统',
                        className: 'selds',
                        content:
                            <div>
                                <EntryConfigRef
                                    value={this.state.dbinfo}
                                    isMultiSelectedEnabled={false}
                                    queryCondition={v => {
                                        return {
                                            pk_org: this.state.pk_org
                                        }
                                    }}
                                    onChange={(v) => {
                                        this.setState({
                                            dbinfo: v,
                                        })
                                    }}
                                />
                            </div>
                        ,
                        beSureBtnClick: actions['beSureBtnClick'].bind(this, this.props),
                        cancelBtnClick: actions['cancelBtnClick'].bind(this),
                        size: 'sm'
                    })
                }

            </div>
        )
    }
}

let TreeForm = createPage({
    billinfo: {
        billtype: 'form',
        pagecode: pageCode,
        bodycode: formId
    },
})(comp)

ReactDOM.render(<TreeForm />, document.querySelector('#app'))
